import React from 'react';
import Link from 'gatsby-link';
import './music.css';
import '../pages/index.css';
import RecordPlayer from '../svg/recordPlayer';


const Music = () => (
  <div className="row newRow">
    <div className="block">
      <div className="rpcContainer">
        <RecordPlayer />
      </div>
    </div>
    <div className="block r"><div className="line"></div>
        <div className="line"></div>
          <div className="header"><h1><span>——</span> music</h1></div>
          <ul className="list musicList">
              <li id="musicLi">Heres a short list of some of my favorite songs recently:
                <li>Travels - khai dreams</li>
                <li>Ghost Town - Kanye West</li>
                <li>Life goes on - Lil Baby</li>
                <li>Forever Always - Peter CottonTale</li>
                <li>Got Friends - GoldLink</li>
                <li>Fallingwater - Maggie Rogers</li>
                <li>Wish - Diplo</li>
            </li>
          </ul>
    </div>
  </div>
)

export default Music



