import React from 'react';
import Link from 'gatsby-link';
import Window from '../svg/window';
import '../pages/index.css';
import './projects.css';


const Projects = () => (
    <div className="row newRow">
      <div className="block">
        <div className="windowDiv wd1"><Window picture=""/></div>
        <div className="windowDiv wd2"><Window picture=""/></div>
        <div className="windowDiv wd3"><Window picture=""/></div>
      </div>
      <div className="block r">
        <div className="line"></div>
        <div className="line"></div>

          <div className="header"><h1><span>——</span> projects</h1></div>
          <ul class="list">
              <li>Example project 1<li>A quick description about this</li></li>
              <li>Example project 2<li>A quick description about this</li></li>
              <li>Example project 3<li>A quick description about this</li></li>
          </ul>

      </div>
    </div>
)

export default Projects



